// frontend/src/components/TranslationsProvider.js
"use client";

// We use the standard import, but because this whole file is a Client Component, it will work.
import { I18nextProvider } from 'react-i18next';
import i18n from '@/i18n'; // Import our i18n configuration

const TranslationsProvider = ({ children }) => {
  return (
    <I18nextProvider i18n={i18n}>
      {children}
    </I18nextProvider>
  );
};

export default TranslationsProvider;